// Main component for the menu display
export default function MenuDisplay() {
  const coffeeRef = useRef<HTMLDivElement | null>(null);
  const dessertRef = useRef<HTMLDivElement | null>(null);

  const handleAdd = (item: MenuItem) => {
    // Get existing cart items from localStorage
    const existingCart = JSON.parse(localStorage.getItem("cart") || "[]");
    
    // Add the new item to the cart
    const updatedCart = [...existingCart, item];
    
    // Save the updated cart back to localStorage
    localStorage.setItem("cart", JSON.stringify(updatedCart));

    // A simple toast notification, no shopping cart functionality included here
    const el = document.createElement("div");
    el.textContent = `${item.name} added to cart âœ…`;
    el.className =
      "fixed right-6 bottom-6 bg-[#3b2b20] text-white px-4 py-2 rounded-md shadow-lg z-[9999] animate-fade-in-out";
    document.body.appendChild(el);
    setTimeout(() => el.remove(), 2200);
  };
  
  // Parallax effect for the hero background
  useEffect(() => {
    const parallax = document.querySelector('[data-parallax]') as HTMLElement;
    if (parallax) {
      const handleScroll = () => {
        const y = window.scrollY * 0.5;
        parallax.style.setProperty('--y', `${y}px`);
      };
      window.addEventListener('scroll', handleScroll);
      return () => {
        window.removeEventListener('scroll', handleScroll);
      };
    }
  }, []);

  return (
    <div className="bg-[#fcf8f3] text-[#3b2b20] font-sans min-h-screen">
      {/* HERO SECTION with PARALLAX EFFECT */}
      <section
        id="home"
        className="relative h-[650px] md:h-[700px] flex items-center justify-center overflow-hidden"
      >
        <div
          className="absolute inset-0 bg-cover bg-center transition-transform duration-100 ease-linear"
          style={{
            backgroundImage: "url('https://images.unsplash.com/photo-1495474472287-4d71bcdd2085?auto=format&fit=crop&w=1600&q=60')",
            transform: "translateY(var(--y))",
            '--y': '0px',
          } as React.CSSProperties}
          data-parallax
        />
        <div className="absolute inset-0 bg-black/40" />
        <div className="relative max-w-5xl mx-6 text-center text-white z-10">
          <p className="text-xl md:text-2xl font-semibold tracking-wider animate-slide-up-1">Our Delicious Menu</p>
          <h1 className="text-4xl md:text-6xl font-extrabold leading-tight mt-2 animate-slide-up-2">
            Freshly Made, Just For You.
          </h1>
        </div>
      </section>

      {/* CATEGORIES */}
      <section className="relative -mt-16 z-20 max-w-6xl mx-auto px-6 animate-fade-in">
        <div className="bg-white rounded-xl shadow-xl p-6 md:p-8 grid grid-cols-2 md:grid-cols-4 gap-6 text-[#3b2b20]">
          <Category icon="â˜•" label="Hot Coffee" />
          <Category icon="ðŸ¥¤" label="Cold Coffee" />
          <Category icon="ðŸµ" label="Cup Coffee" />
          <Category icon="ðŸ°" label="Dessert" />
        </div>
      </section>
      
      {/* SPECIALS - COFFEE */}
      <section id="coffee" className="max-w-6xl mx-auto px-6 mt-16 animate-fade-in-delay">
        <h2 className="text-3xl font-bold text-center mb-10">Our Special Coffee</h2>
        <div className="relative">
          <div
            ref={coffeeRef}
            className="flex gap-6 overflow-x-auto no-scrollbar pb-6 scroll-smooth"
            style={{ scrollSnapType: "x mandatory" }}
          >
            {coffeeItems.map((item) => (
              <Card key={item.id} item={item} onAdd={handleAdd} />
            ))}
          </div>
          <ScrollButtons scrollRef={coffeeRef} />
        </div>
      </section>
      
      {/* SPECIALS - DESSERT */}
      <section id="dessert" className="max-w-6xl mx-auto px-6 mt-12 animate-fade-in-delay">
        <h2 className="text-3xl font-bold text-center mb-10">Our Special Dessert</h2>
        <div className="relative">
          <div
            ref={dessertRef}
            className="flex gap-6 overflow-x-auto no-scrollbar pb-6 scroll-smooth"
            style={{ scrollSnapType: "x mandatory" }}
          >
            {dessertItems.map((item) => (
              <Card key={item.id} item={item} onAdd={handleAdd} />
            ))}
          </div>
          <ScrollButtons scrollRef={dessertRef} />
        </div>
      </section>

      {/* CUSTOM CSS FOR ANIMATIONS */}
      <style jsx>{`
        .no-scrollbar {
          -ms-overflow-style: none;
          scrollbar-width: none;
        }
        .no-scrollbar::-webkit-scrollbar {
          display: none;
        }
        @keyframes fadeIn {
          from { opacity: 0; transform: translateY(20px); }
          to { opacity: 1; transform: translateY(0); }
        }
        .animate-fade-in {
          animation: fadeIn 0.8s ease-out forwards;
        }
        .animate-fade-in-delay {
          animation: fadeIn 0.8s ease-out forwards 0.3s;
          opacity: 0;
        }
        @keyframes slideUp {
          from { opacity: 0; transform: translateY(30px); }
          to { opacity: 1; transform: translateY(0); }
        }
        .animate-slide-up-1 { animation: slideUp 0.8s ease-out forwards; }
        .animate-slide-up-2 { animation: slideUp 0.8s ease-out forwards 0.2s; opacity: 0; }
        
        @keyframes fadeInOut {
            0% { opacity: 0; transform: translateY(6px); }
            10% { opacity: 1; transform: translateY(0); }
            90% { opacity: 1; transform: translateY(0); }
            100% { opacity: 0; transform: translateY(-6px); }
        }
        .animate-fade-in-out {
            animation: fadeInOut 2.2s ease-in-out forwards;
        }
      `}</style>
    </div>
  );
}

/* --- small helper components with prop types --- */

function Category({ icon, label }: { icon: string; label: string }) {
  return (
    <div className="flex flex-col items-center gap-2 transition transform hover:scale-110">
      <div className="w-16 h-16 rounded-full bg-white shadow-md flex items-center justify-center text-3xl">
        {icon}
      </div>
      <p className="text-sm font-medium">{label}</p>
    </div>
  );
}

function Card({ item, onAdd }: { item: MenuItem; onAdd: (i: MenuItem) => void }) {
  return (
    <article
      className="min-w-[280px] max-w-[340px] bg-white rounded-xl shadow-lg overflow-hidden flex-shrink-0 transition-transform hover:scale-105"
      style={{ scrollSnapAlign: "center" }}
    >
      <div className="relative">
        <img
          src={item.image}
          alt={item.name}
          className="w-full h-52 object-cover transition-transform duration-300 hover:scale-110"
        />
        <button
          aria-label="favorite"
          className="absolute top-4 right-4 bg-white/80 p-2 rounded-full shadow-md text-xl text-gray-500 transition hover:text-red-500"
        >
          â™¥
        </button>
        <div className="absolute left-4 bottom-4 bg-white/90 px-4 py-2 rounded-full text-lg font-bold shadow-md">
          â‚¹{item.price}
        </div>
      </div>
      <div className="p-5">
        <h3 className="text-xl font-bold">{item.name}</h3>
        <p className="text-sm text-gray-600 mt-2">{item.description}</p>
        <button
          onClick={() => onAdd(item)}
          className="mt-4 w-full bg-[#3b2b20] text-white font-semibold py-3 rounded-full transition transform hover:scale-95"
        >
          Add to Cart
        </button>
      </div>
    </article>
  );
}

function ScrollButtons({ scrollRef }: { scrollRef: React.RefObject<HTMLDivElement> }) {
  const scroll = (dir: "left" | "right") => {
    if (!scrollRef.current) return;
    const amount = scrollRef.current.clientWidth * 0.8;
    scrollRef.current.scrollBy({ left: dir === "left" ? -amount : amount, behavior: "smooth" });
  };
  return (
    <div className="absolute top-1/2 left-0 right-0 transform -translate-y-1/2 flex justify-between px-2 md:px-0">
      <button
        aria-label="scroll left"
        onClick={() => scroll("left")}
        className="w-12 h-12 rounded-full bg-white/80 shadow-lg flex items-center justify-center text-2xl text-gray-700 transition transform hover:scale-110"
      >
        â€¹
      </button>
      <button
        aria-label="scroll right"
        onClick={() => scroll("right")}
        className="w-12 h-12 rounded-full bg-white/80 shadow-lg flex items-center justify-center text-2xl text-gray-700 transition transform hover:scale-110"
      >
        â€º
      </button>
    </div>
  );
}